import React, { useEffect, useMemo } from "react"
import useKakaoEvent from "../hooks/useKakaoEvent"
import useMap from "../hooks/useMap"

interface LatLng {
  lat: number
  lng: number
}

export interface PolylineProps {
  path: LatLng[] | LatLng[][]
  /**
   *  
   */
  endArrow?: boolean
  /**
   *     (: 3)
   */
  strokeWeight?: number
  /**
   * #xxxxxx    (: #F10000)
   */
  strokeColor?: string
  /**
   *   (0-1) (: 0.6)
   */
  strokeOpacity?: number
  /**
   *   (: solid)
   */
  strokeStyle?: kakao.maps.StrokeStyles
  /**
   *  z-index  
   */
  zIndex?: number

  /**
   *    .
   */
  onMouseover?: (
    target: kakao.maps.Polyline,
    mouseEvent: kakao.maps.event.MouseEvent
  ) => void
  /**
   *    .
   */
  onMouseout?: (
    target: kakao.maps.Polyline,
    mouseEvent: kakao.maps.event.MouseEvent
  ) => void
  /**
   *   .
   */
  onMousemove?: (
    target: kakao.maps.Polyline,
    mouseEvent: kakao.maps.event.MouseEvent
  ) => void
  /**
   *    .
   */
  onMousedown?: (
    target: kakao.maps.Polyline,
    mouseEvent: kakao.maps.event.MouseEvent
  ) => void
  /**
   *  .
   */
  onClick?: (
    target: kakao.maps.Polyline,
    mouseEvent: kakao.maps.event.MouseEvent
  ) => void
  /**
   *    .
   */
  onCreate?: (target: kakao.maps.Polyline) => void
}

/**
 * Map   .
 */
const Polyline: React.FC<PolylineProps> = ({
  path,
  endArrow,
  onClick,
  onMousedown,
  onMousemove,
  onMouseout,
  onMouseover,
  onCreate,
  strokeColor,
  strokeOpacity,
  strokeStyle,
  strokeWeight,
  zIndex,
}) => {
  const map = useMap(`Polyline`)

  const polyLinePath = useMemo(() => {
    if ((path as LatLng[]).every((v) => v instanceof Array)) {
      return (path as LatLng[][]).map((v) => {
        return v.map((p) => new kakao.maps.LatLng(p.lat, p.lng))
      })
    }
    return (path as LatLng[]).map((v) => {
      return new kakao.maps.LatLng(v.lat, v.lng)
    })
  }, [path])

  const polyline = useMemo(() => {
    return new kakao.maps.Polyline({
      path: polyLinePath,
      endArrow,
      strokeColor,
      strokeOpacity,
      strokeStyle,
      strokeWeight,
      zIndex,
    })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  useEffect(() => {
    polyline.setMap(map)
    return () => polyline.setMap(null)
  }, [map, polyline])

  useEffect(() => {
    if (onCreate) onCreate(polyline)
  }, [polyline, onCreate])

  useEffect(() => {
    polyline.setOptions({
      endArrow,
      strokeColor,
      strokeOpacity,
      strokeStyle,
      strokeWeight,
    })
  }, [
    polyline,
    endArrow,
    strokeColor,
    strokeOpacity,
    strokeStyle,
    strokeWeight,
  ])

  useEffect(() => {
    polyline.setPath(polyLinePath)
  }, [polyline, polyLinePath])

  useEffect(() => {
    if (zIndex) polyline.setZIndex(zIndex)
  }, [polyline, zIndex])

  useKakaoEvent(polyline, "mouseover", onMouseover)
  useKakaoEvent(polyline, "mouseout", onMouseout)
  useKakaoEvent(polyline, "mousemove", onMousemove)
  useKakaoEvent(polyline, "mousedown", onMousedown)
  useKakaoEvent(polyline, "click", onClick)

  return null
}

export default Polyline
